<template>
  <div class="room-list">
    <b-menu>
      <b-menu-list label="Channels">
        <b-menu-item
          v-for="room in rooms"
          :key="room.name"
          :active="activeRoom.id == room.id"
          :label="'# ' + room.name"
          @click="onChange(room)"
        >
        </b-menu-item>
      </b-menu-list>
    </b-menu>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  name: "RoomList",
  computed: {
    ...mapState(["rooms", "activeRoom"]),
  },
  methods: {
    ...mapActions([
      'changeRoom'
    ]),
    onChange(room) {
      this.changeRoom(room.id);
    }
  }
};
</script>

