<template>
  <div class="message-form">
    <small class="has-text-info">@{{ user.username }}</small>
    <form @submit.prevent="onSubmit" style="position: relative">
      <b-loading
        :is-full-page="false"
        :active.sync="sending"
        :can-cancel="false"
      />
      <b-message type="is-danger" v-show="hasError">
        {{ error }}
      </b-message>
      <b-field>
        <b-input
          id="message-input"
          type="textarea"
          v-model="message"
          @input="isTyping"
          placeholder="Enter Message"
          autocomplete="off"
          required
        />
      </b-field>
      <b-field>
        <button class="button is-info is-pulled-right" type="submit">
          Send
        </button>
      </b-field>
    </form>
  </div>
</template>

<script>
import { mapActions, mapState, mapGetters } from 'vuex'
import { isTyping } from '../chatkit.js'

export default {
  name: "message-form",
  data() {
    return {
      message: ""
    };
  },
  computed: {
    ...mapState(["user", "sending", "error", "activeRoom"]),
    ...mapGetters(["hasError"])
  },
  methods: {
    ...mapActions([
      'sendMessage',
    ]),
    async onSubmit() {
      const result = await this.sendMessage(this.message);
      if(result) {
        this.message = '';
      }
    },
     async isTyping() {
      await isTyping(this.activeRoom.id);
    }
  }
};
</script>

