<template>
  <div class="message-list">
    <p class="is-size-4">Messages</p>
    <hr />
    <div
      id="chat-messages"
      class="message-group"
      v-chat-scroll="{ smooth: true }"
    >
      <b-message v-for="(message, index) in messages" :key="index">
        <p>
          {{ message.name }}
        </p>
        <p class="is-size-7 has-text-info">@{{ message.username }}</p>
        <p>
          {{ message.text }}
        </p>
        <p class="is-size-7 is-pulled-right has-text-grey">
          {{ message.date }}
        </p>
      </b-message>
      <div class="user-typing">
        <small class="text-muted" v-if="userTyping"
          >@{{ userTyping }} is typing....</small
        >
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "message-list",
  computed: {
    ...mapState(["messages", "userTyping"])
  }
};
</script>

<style>
.message-group {
  height: 55vh !important;
  overflow-y: scroll;
}
</style>
