<template>
  <div class="user-list">
    <b-menu>
      <b-menu-list label="Members">
        <b-menu-item v-for="user in users" :key="user.name">
          <template slot="label">
            <div>
              <span>
                {{ user.name }}
              </span>
              <span
                v-if="user.presence"
                class="tag is-rounded"
                :class="statusColor(user.presence)"
              >
                {{ user.presence }}
              </span>
            </div>
          </template>
        </b-menu-item>
      </b-menu-list>
    </b-menu>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "user-list",
  computed: {
    ...mapState(["loading", "users"])
  },
  methods: {
    statusColor(status) {
      return status === "online" ? "is-success" : "is-warning";
    }
  }
};
</script>

<style></style>
