<template>
  <div class="login-form">
    <p class="is-size-5 has-text-centered">Login</p>
    <hr />
    <form @submit.prevent="onSubmit">
      <b-message type="is-danger" v-show="hasError">
        {{ error }}
      </b-message>
      <b-field grouped>
        <b-field label="User Name" expanded>
          <b-input
            id="userInput"
            v-model="userId"
            type="text"
            placeholder="Enter user name"
            :disabled="loading"
            autocomplete="off"
            required
          ></b-input>
        </b-field>
      </b-field>
      <button
        type="submit"
        class="button is-info is-fullwidth"
        :class="loading ? 'is-loading' : ''"
        :disabled="isValid"
      >
        Login
      </button>
    </form>
  </div>
</template>

<script>
import { mapState, mapGetters, mapActions  } from "vuex";
export default {
  name: "login-form",
  data() {
    return {
      userId: ""
    };
  },
  computed: {
    isValid() {
      const result = this.userId.length < 3;
      return result ? result : this.loading;
    },
    ...mapState(["loading", "error"]),
    ...mapGetters(["hasError"]),
  },
  methods: {
    ...mapActions([
      'login'
    ]),
    async onSubmit() {
      const result = await this.login(this.userId);
      if(result) {
        this.$router.push('chat');
      }
    }
  }
};
</script>
